.feedback {
  &-main-container {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: transparent;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &-container{
    background-color: white;
    padding: 100px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    text-align: center;
  }

  &-title {
    font: {
      size: 25px;
      weight: bold;
    }
    span{
        color:rgb(75, 0, 130);
    }
    margin-bottom: 20px;
  }

  &-subtitle {
    font-size: 18px;
    margin-bottom: 10px;
  }

  &-text {
    color:grey;
    span{
        color: #13EBA2;
        font-size: 18px;
    }  
  }
}
